import { Form, Col, Row} from 'react-bootstrap';
import {useReducer} from 'react'
import {stateReducer, initialState, List } from '../Reducers/createWorks';
import {Create_Work,  Add_List} from '../Reducers/actions/createdWork'
import Works from '../works/Works';


const Input = () => {
    
   
    
    const [state, dispatch] = useReducer(stateReducer, initialState)
    
   
   
    const AddWork = (e)=>{
        e.preventDefault()
        const {value} = e.target[0]
        if(value.trim()){
            
            dispatch({type: Create_Work, payload: {data: value}})
            dispatch({type: Add_List, state})
          
            e.target.reset()
        }
    }
    
    

    return ( 
     <>   
    <Row className=" mb-5 justify-content-center" >
        
            
               
                    <Form onSubmit={AddWork} className="d-flex justify-content-center">
                        

                            <Col className="col-8">
                                <Form.Group>
                                    <Form.Control
                                        type='text'                      
                                        placeholder='Buscar'                        
                                />
                                </Form.Group>
                            </Col>
                            <Col className="col-1">
                                <button className="btn btn-primary" type="submit">Add</button>
                            </Col>

                        
                    </Form>
                
                    
                

                
                

        
        
    </Row>
    <Row>
        
            <Works List={List} dispatch={dispatch}/>  
        
    </Row>
    </>
     );
}
 
export default Input;