import {Create_Work, Add_List, Delete_Work, Status} from './actions/createdWork';

export const initialState = {
    works:
        {   description:'', status: false, id: 1  }
    
}
export let List=[];

export const stateReducer = (state= initialState, action) => {
    switch(action.type){
        case Create_Work:               
                              
                return {
                    ...initialState.works,
                    description: action.payload.data,
                    id: Math.random()
                }  
        case Add_List:
            List.unshift(state)
            return state

        case Delete_Work:
            
            return List= List.filter(lista=> lista.id !== action.payload.data  ) 

        case Status:
               return List.map(lista=> lista.id === action.payload.data?
                    lista.status? lista.status= false: lista.status=true: null)                
                
                
        default:
            return state
    }
    
}
 
