import {Col, Row} from 'react-bootstrap'
import { Delete_Work, Status } from '../Reducers/actions/createdWork';

import './../../App.css'



const Works = ({List, dispatch}) => {
    
  
    const hanldeChange = (identificador)=>{
        dispatch({type: Status, payload: {data: identificador} })
    }

    return (      

           
            <Col className="Listado rounded">
              <ul className="list-group">
                {List && List.length>0 &&
                        List.map(lista =>
                            <Row key={lista.id} className="justify-content-center align-items-center text-center">
                                    <Col  className="col-10 p-0">
                                    
                                        <li   className={lista.status   ? "list-group-item list-group-item-success mb-1"
                                                                        : "list-group-item list-group-item-danger mb-1"}>
                                        {lista.description}
                                        </li>
                                    </Col>
                                    <Col className="col-1 text-center p-0">
                                        <input className="form-check-input" onChange={()=>hanldeChange(lista.id)} type="checkbox" value=""/>
                                    </Col>
                                    
                                    <Col className="col-1 p-0 text-center">
                                        <button type="button" className="btn btn-danger" onClick={()=>dispatch({type:Delete_Work, payload: {data: lista.id}})}>X</button>
                                    </Col>
                        
                        
                            </Row>)
                    }  
               </ul>           
            </Col>

            
                
            
                
          

        
        
     );
}
 
export default Works;